import api from 'axios'
import store from './store'
import router from './router'
import {Loading, Message} from 'element-ui'

api.defaults.baseURL = process.env.VUE_APP_API_HOST

let isRefreshing = false // token
let requests = [] // token
let loading
const noLoading = [ // loading
  '/tokens',
  '/tokens/info'
]
api.interceptors.request.use(config => {
  config.headers['Accept-Language'] = store.state.lang
  if (!noLoading.includes(config.url)) {
    loading = Loading.service({
      fullscreen: true,
      lock: true
    })
  }
  let token = store.state.token
  if (token) {
    let now = new Date().getTime() / 1000

    // token
    if (config.url !== '/tokens' && now >= token.expired_at) {
      // token
      if (!isRefreshing) {
        isRefreshing = true
        api.request({
          url: '/tokens',
          method: 'put',
          headers: {Authorization: token.type + ' ' + token.token}
        }).then(res => {
          let newToken = null
          if (res) {
            newToken = {
              token: res.token,
              type: res.type,
              expired_at: res.expired_at,
              user_type: token.user_type
            }
            localStorage.JWT_FXHOS_ADMIN_TOKEN = JSON.stringify(newToken)
            store.commit('saveToken', newToken)
          }
          requests.forEach(requestItem => {
            let {resolve, conf} = requestItem
            if (newToken) {
              conf.headers['Authorization'] = newToken.type + ' ' + newToken.token
            }
            resolve(conf)
          })
          requests = []
          isRefreshing = false
        })
      }

      // tokentokenresolve
      return new Promise(resolve => {
        requests.push({resolve, conf: config})
      })
    }

    // token
    config.headers['Authorization'] = token.type + ' ' + token.token
  }
  return config
}, error => {
  loading && loading.close()
  // 
  return Promise.reject(error)
})

api.interceptors.response.use(res => {
  loading && loading.close()
  let {data} = res
  if (data.code !== 200) {
    if (data.code === 401) {
      Message.error('')
      localStorage.removeItem('JWT_FXHOS_ADMIN_TOKEN')
      global.location.reload()
    } else if (data.code === 404) {
      router.push('/404')
    } else if (data.code === 422) {
      for (const key in data.data) {
        let err = data.data[key]
        if (typeof err === 'string') Message.error(err)
        else Message.error(err[0])
        break
      }
    } else if (data.code === 500 && typeof data.data === 'string') {
      Message.error(data.data)
    } else {
      Message.error('')
    }
  } else {
    loading && loading.close()
    return data.data || true
  }
})

export const http = api

export default {
  install(Vue) {
    Vue.api = api
    Vue.prototype.$api = api
  }
}
